import { INPUT_BOX_STYLES } from "assets/base-theme";
import BaseInput from "components/base/BaseInput";
import InputError from "components/InputError";
import Label from "components/Label";
import { useTranslation } from "react-i18next";
import { INPUT_STYLE_TYPE } from "utils/Constant";
import { generateClasses } from "utils/Util";

const InputAddon = (props: any) => {
  const {
    addOn,
    variant,
    styleObj,
    args,
    labelClassNames,
    labelStyleObj,
    validation,
    hideErrorRow,
    errorClassNames,
    isSuffix,
    hideLabel,
    ...BaseInputProps
  } = props;

  const { placeholder, label } = BaseInputProps;
  const getClassNames = () => {
    const styleObject = {
      ...INPUT_BOX_STYLES,
      overrideStyle: styleObj,
      overrideClasses: props.className,
      isDisabled: props.disabled,
      isInvalid: props.validation ? true : false,
      removeStyleProperty: [],
      removeClasses: [],
    };

    return generateClasses(styleObject, variant);
  };

  const { t } = useTranslation();

  let placeholderLabel: string;

  if (placeholder !== undefined) {
    placeholderLabel = t(placeholder, args);
  } else {
    // Handle the case when the label is undefined
    placeholderLabel = ""; // Or provide a default value or appropriate handling
  }
  return (
    <div className="flex flex-col gap-1">
      {!hideLabel ? (
        <Label
          label={label}
          labelClassNames={labelClassNames}
          labelStyleObj={labelStyleObj}
          isInvalid={validation}
        />
      ) : null}

      <div className={getClassNames() + " flex items-center !pt-0 !pl-0 !pb-0 !pr-0  "}>
        {!isSuffix ? (
          <div className="bg-gray-100 w-max-fit px-2 h-full rounded-l-md flex items-center">
            {addOn ?? "+91"}
          </div>
        ) : null}

        <BaseInput
         {...BaseInputProps}
          className={`outline-none w-full h-fit px-2 ${props.className??""}`}
          hideLabel
          hideErrorRow
          placeholder={placeholderLabel}
        />
        {isSuffix ? (
          <div className="bg-gray-100 w-max-fit px-2 h-full rounded-r-md flex items-center">
            {addOn}
          </div>
        ) : null}
      </div>

      {!hideErrorRow && (
        <InputError
          validation={validation}
          hideErrorRow={hideErrorRow}
          errorClassNames={errorClassNames}
        />
      )}
    </div>
  );
};

export default InputAddon;
